export const slider = () => {
  const gallerySlider = new Swiper(".gallery-slider", {
    spaceBetween: 8,
    centeredSlides: true,
    navigation: {
      nextEl: ".swiper-button-next",
      prevEl: ".swiper-button-prev",
    },
    loop: true,
    slidesPerView: 3.2,

    breakpoints: {
      769: {
        spaceBetween: 12,
        slidesPerView: 7.5,
      },
    },
  });
};
